export class WalletStatement {
    customerId: string;
    creditOrdebit: string;
    paymentMode: string;
    viewBy: string;
    customFromDate: string;
    customToDate: string;
    transactionType: string;
    collectioIdOrPolicyNumber: string;
    transactionNumber: string;
    instrumentNumber: string;
    amountRangeFrom: string;
    amountRangeTo: string;
    
    constructor(customerId: string,
        creditOrdebit: string,
        paymentMode: string,
        viewBy: string,
        customFromDate: string,
        customToDate: string,
        transactionType: string,
        collectioIdOrPolicyNumber: string,
        transactionNumber: string,
        instrumentNumber: string,
        amountRangeFrom: string,
        amountRangeTo: string,
        ) {
        this.customerId = customerId;
        this.creditOrdebit = creditOrdebit;
        this.paymentMode = paymentMode;
        this.viewBy = viewBy;
        this.customFromDate = customFromDate;
        this.customToDate = customToDate;
        this.transactionType = transactionType;
        this.collectioIdOrPolicyNumber = collectioIdOrPolicyNumber;
        this.transactionNumber = transactionNumber;
        this.instrumentNumber = instrumentNumber;
        this.amountRangeFrom = amountRangeFrom;
        this.amountRangeTo = amountRangeTo;
       
    }
}