import { Component, OnInit } from "@angular/core";
import { HttpErrorResponse } from "@angular/common/http";
import { CreateQuoteService } from 'src/app/core/services/create-quote-service';
import { WalletStatement } from 'src/app/core/models/walletstatement';
import { WalletStatementService } from 'src/app/core/services/walletstatement.service';

@Component({
  selector: "app-walletstatement",
  templateUrl: "./walletstatement.component.html",
  styleUrls: ["./walletstatement.component.css"],
})
export class WalletstatementComponent implements OnInit {
  walletResponse: any;
  dataForPdf: any;
  walletResponseForGrid: any;
  userID: number;
  walletStatementParams = new WalletStatement('', 'All', 'All', 'All', '', '', 'All', '', '', '', '', '');
  transactiontypes: any;
  blob: Blob;
  paymentmodes: any;
  rotateIcon: boolean = false;
  rotate: boolean = false;
  constructor(private walletStatementService: WalletStatementService, private createQuoteService: CreateQuoteService) {

  }

  walletStatementParams1=
  {
    "userID": 1,
    "policyNumber": null,
    "transactionNumber": null,
    "paymentMode": null,
    "transactionType": null,
    "type": null,
    "instrumentNumber": null
  }
  ngOnInit() {
    this.getVirtualWallet();
    this.getTransactiontypes();
    this.getDataForWalletStatement();
    this.getpaymentmodes();
  }

  getVirtualWallet() {
    this.userID = 1;
    this.createQuoteService.getVirtualWallet(this.userID).subscribe(
      (res) => {
        this.walletResponse = res;
        this.walletResponse = this.walletResponse.closingBalance;
        console.log(res);
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }

  getDataForWalletStatement() {
    //this.walletStatementParams.customerId = 'IMD1000444';
    this.walletStatementService.getDataForWalletStatement(this.walletStatementParams1).subscribe(
      (res) => {
        this.walletResponseForGrid = res;
        console.log(res);
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }

  clear()
  {
    this.walletStatementParams1.policyNumber = null,
    this.walletStatementParams1.instrumentNumber = null,
    this.walletStatementParams1.paymentMode = null,
    this.walletStatementParams1.transactionNumber = null,
    this.walletStatementParams1.transactionType = null,
    this.walletStatementParams1.type = null,
    this.walletResponseForGrid = null
  }

  getpaymentmodes(){
    this.walletStatementService.getpaymentmodes().subscribe(
      (res) => {
        this.paymentmodes = res;
        console.log(res);
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }

  getTransactiontypes() {
    this.walletStatementService.getTransactiontypes().subscribe(
      (res) => {
        this.transactiontypes = res;
        console.log(res);
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }

  exportDataToExcel() {
    //this.walletStatementParams.customerId = 'IMD1000444';
    this.walletStatementService.exportDataToExcel(this.walletStatementParams1).subscribe(
      (res) => {
        this.dataForPdf = res;
        this.blob = new Blob([  this.dataForPdf], { type: 'application/vnd.ms-excel' });
        var downloadURL = window.URL.createObjectURL(res);
        var link = document.createElement('a');
        link.href = downloadURL;
        link.download = "WallateStatement.xls";
        link.click();
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }

  exportDataToPdf() {
    //this.walletStatementParams.customerId = 'IMD1000444';
    this.walletStatementService.exportDataToPdf(this.walletStatementParams1).subscribe(
      (res) => {
        this.dataForPdf = res;
        this.blob = new Blob([  this.dataForPdf], { type: 'application/pdf' });
        var downloadURL = window.URL.createObjectURL(res);
        var link = document.createElement('a');
        link.href = downloadURL;
        link.download = "WallateStatement.pdf";
        link.click();
      },
      (error: HttpErrorResponse) => {
        // alert(error.error.text);
      }
    );
  }

}
