import { HttpClient, HttpParams, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
let headers1: HttpHeaders = new HttpHeaders();
headers1 = headers1.append('Content-Type', 'application/json');
const httpOptions = {
    responseType: 'blob' as 'json',
};
@Injectable({ providedIn: "root" })
export class WalletStatementService {
    baseUrl = `${environment.serviceBaseUrl}`;

    constructor(private httpClient: HttpClient) { }

    getDataForWalletStatement(walletStatementParams1) {
        return this.httpClient.post(
            "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7776/walletStatement/",
            walletStatementParams1,{ headers: headers1 }
        );

    }

    getpaymentmodes() {
        return this.httpClient.get(
            this.baseUrl + "api/wallet/v1/paymentmodes"
        )
    }

    getTransactiontypes() {
        return this.httpClient.get(
            this.baseUrl + "api/wallet/v1/transactiontypemasters"
        )
    }

    exportDataToExcel(data) {
        return this.httpClient.post(
            "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7776/walletStatement/export/excel",
            data, httpOptions
        );
    }

    exportDataToPdf(data) {
        return this.httpClient.post(
            "http://ec2-13-233-60-5.ap-south-1.compute.amazonaws.com:7776/walletStatement/export/pdf ", data
            , httpOptions);
    }
}
